=============================================\n//  $   SHOW STYLE\n//  ----------------------------------------------------------------------------\n.d-tooltip--show {\n    transform: translate(0, 0);\n    visibility: visible;\n    opacity: 1;\n    transition:\n        visibility 0s 0s,\n        transform 200ms var(--ttf-in-out) 10ms,\n        opacity 200ms var(--ttf-in-out) 10ms;\n}\n\n//  ============================================================================\n//  $   HOVER AND FOCUS STYLES\n//  ----------------------------------------------------------------------------\n.d-tooltip--hover {\n    .d-tooltip {\n        &:extend(.d-tooltip--hide);\n    }\n\n    &:hover,\n    &:focus-visible {\n        .d-tooltip {\n            &:extend(.d-tooltip--show);\n        }\n    }\n}\n\n//  ============================================================================\n//  $   TOOLTIP DIRECTIONS\n//  ============================================================================\n//  $$  TOP\n//  ----------------------------------------------------------------------------\n.d-tooltip__arrow-tippy--bottom-start,\n.d-tooltip__arrow-tippy--bottom,\n.d-tooltip__arrow-tippy--bottom-end,\n.d-tooltip__arrow--top-left,\n.d-tooltip__arrow--top-center,\n.d-tooltip__arrow--top-right {\n  top: calc(100% + var(--su12));\n  transform: translateY(var(--su16));\n\n  &::after {\n    top: var(--sun6);\n    border-top-width: 0;\n    border-bottom-color: var(--tooltip--bgc);\n  }\n}\n\n//  $$  BOTTOM\n//  ----------------------------------------------------------------------------\n.d-tooltip__arrow-tippy--top-start,\n.d-tooltip__arrow-tippy--top,\n.d-tooltip__arrow-tippy--top-end,\n.d-tooltip__arrow--bottom-left,\n.d-tooltip__arrow--bottom-center,\n.d-tooltip__arrow--bottom-right {\n  bottom: calc(100% + var(--su12));\n  transform: translateY(var(--sun16));\n\n  &::after {\n    bottom: var(--sun6);\n    border-top-color: var(--tooltip--bgc);\n    border-bottom-width: 0;\n  }\n}\n\n//  $$  TOP / BOTTOM LEFT\n//  ----------------------------------------------------------------------------\n.d-tooltip__arrow-tippy--bottom-start,\n.d-tooltip__arrow-tippy--top-start,\n.d-tooltip__arrow--top-left,\n.d-tooltip__arrow--bottom-left {\n  left: var(--sun2);\n\n  &::after {\n    left: var(--su16);\n  }\n}\n\n//  $$  TOP / BOTTOM CENTER\n//  ----------------------------------------------------------------------------\n.d-tooltip__arrow-tippy--bottom,\n.d-tooltip__arrow-tippy--top,\n.d-tooltip__arrow--top-center,\n.d-tooltip__arrow--bottom-center {\n  &::after {\n    left: 50%;\n    margin-left: var(--sun6);\n  }\n}\n\n//  $$  TOP / BOTTOM RIGHT\n//  ----------------------------------------------------------------------------\n.d-tooltip__arrow-tippy--bottom-end,\n.d-tooltip__arrow-tippy--top-end,\n.d-tooltip__arrow--top-right,\n.d-tooltip__arrow--bottom-right {\n  right: var(--sun2);\n\n  &::after {\n    right: var(--su16);\n  }\n}\n\n//  $$  RIGHT\n//  ----------------------------------------------------------------------------\n.d-tooltip__arrow-tippy--left-start,\n.d-tooltip__arrow-tippy--left,\n.d-tooltip__arrow-tippy--left-end,\n.d-tooltip__arrow--right-top,\n.d-tooltip__arrow--right-center,\n.d-tooltip__arrow--right-bottom {\n  right: calc(100% + var(--su12));\n  transform: translateX(var(--sun16));\n\n  &::after {\n    right: var(--sun6);\n    border-right-width: 0;\n    border-left-color: var(--tooltip--bgc);\n  }\n}\n\n//  $$  LEFT\n//  ----------------------------------------------------------------------------\n.d-tooltip__arrow-tippy--right-start,\n.d-tooltip__arrow-tippy--right,\n.d-tooltip__arrow-tippy--right-end,\n.d-tooltip__arrow--left-top,\n.d-tooltip__arrow--left-center,\n.d-tooltip__arrow--left-bottom {\n  left: calc(100% + var(--su12));\n  transform: translateX(var(--su16));\n\n  &::after {\n    left: var(--sun6);\n    border-right-color: var(--tooltip--bgc);\n    border-left-width: 0;\n  }\n}\n\n//  $$  RIGHT / LEFT TOP\n//  ----------------------------------------------------------------------------\n.d-tooltip__arrow-tippy--right-start,\n.d-tooltip__arrow-tippy--left-start,\n.d-tooltip__arrow--right-top,\n.d-tooltip__arrow--left-top {\n  top: var(--sun1);\n\n  &::after {\n    top: var(--su8);\n  }\n}\n\n//  $$  RIGHT / LEFT CENTER\n//  ----------------------------------------------------------------------------\n.d-tooltip__arrow-tippy--right,\n.d-tooltip__arrow-tippy--left,\n.d-tooltip__arrow--right-center,\n.d-tooltip__arrow--left-center {\n  &::after {\n    top: 50%;\n    margin-top: var(--sun6);\n  }\n}\n\n//  $$  RIGHT / LEFT BOTTOM\n//  ----------------------------------------------------------------------------\n.d-tooltip__arrow-tippy--right-end,\n.d-tooltip__arrow-tippy--left-end,\n.d-tooltip__arrow--right-bottom,\n.d-tooltip__arrow--left-bottom {\n  bottom: var(--sun1);\n\n  &::after {\n    bottom: var(--su8);\n  }\n}\n","//@import (reference) '../variables/icons.less';\n\n//\n//  DIALTONE\n//  COMPONENTS: TOGGLE\n//\n//  These are toggle classes for Dialpad's design system Dialtone.\n//  For further documentation of these and other classes,\n//  visit https://dialpad.design/components/toggle\n//\n//  TABLE OF CONTENTS\n//  • BASE STYLE\n//  • TOGGLE VARIANTS\n//  • CHECKED TOGGLE\n//  • INDETERMINATE TOGGLE\n//  • FOCUSED TOGGLE\n//  • DISABLED TOGGLE\n//\n//\n//  ============================================================================\n//  $   BASE STYLE\n//  ----------------------------------------------------------------------------\n.d-toggle {\n  //  Component specific CSS Vars\n  //  ------------------------------------------------------------------------\n  --toggle-ta: var(--ttf-in-out);\n  --toggle-ts: var(--td200);\n  --toggle-bgc: var(--black-400);\n  --toggle-height: var(--su24);\n  --toggle-width: var(--su48);\n  --toggle-icon-size: var(--su16);\n\n  //  $$  BASE STYLE\n  //  ----------------------------------------------------------------------------\n  position: relative;\n  display: inline-block;\n  box-sizing: border-box;\n  width: var(--toggle-width);\n  height: var(--toggle-height);\n  padding: 0;\n  line-height: var(--lh4);\n  vertical-align: middle;\n  background-color: var(--toggle-bgc);\n  border: var(--su1) solid var(--toggle-bgc);\n  border-radius: var(--toggle-width);\n  cursor: pointer;\n\n  &__inner {\n    position: absolute;\n    top: calc(var(--su4) - var(--su1));\n    left: var(--su24);\n    width: var(--toggle-icon-size);\n    height: var(--toggle-icon-size);\n\n    .d-toggle--small & {\n      top: var(--su1);\n      left: calc(var(--su16) + var(--su1));\n    }\n\n    .d-toggle--indeterminate & {\n      display: none;\n    }\n\n    color: var(--white);\n    background-image: url('data:image/svg+xml;base64,');\n    background-repeat: no-repeat;\n    background-size: cover;\n  }\n\n  &::after {\n    position: absolute;\n    top: var(--su1);\n    left: var(--su1);\n    width: calc(var(--toggle-icon-size) + var(--su4));\n    height: calc(var(--toggle-icon-size) + var(--su4));\n    background-color: var(--white);\n    border-radius: var(--br-circle);\n    cursor: pointer;\n    transition: left var(--toggle-ts) var(--toggle-ta);\n    content: ' ';\n  }\n\n  //  $$  TOGGLE VARIANTS\n  //  ----------------------------------------------------------------------------\n  &--small {\n    --toggle-height: calc(var(--su16) + var(--su2));\n    --toggle-width: calc(var(--su32) + var(--su4));\n    --toggle-icon-size: calc(var(--su12) + var(--su2));\n\n    &::before {\n      position: absolute;\n      top: calc(var(--su4) * -1);\n      right: 0;\n      bottom: calc(var(--su4) * -1);\n      left: 0;\n      content: '';\n    }\n  }\n\n  &--small::after {\n    width: var(--toggle-icon-size);\n    height: var(--toggle-icon-size);\n  }\n\n  //  $$  CHECKED TOGGLE\n  //  ----------------------------------------------------------------------------\n  &--checked {\n    --toggle-bgc: var(--primary-color);\n\n    .d-toggle__inner {\n      left: var(--su6);\n      background-image: url('data:image/svg+xml;base64,');\n    }\n\n    &.d-toggle--small .d-toggle__inner {\n      left: calc(var(--su2) + var(--su1));\n    }\n\n    &::after {\n      left: calc(var(--su24) + var(--su1));\n    }\n\n    &.d-toggle--small::after {\n      right: var(--su1);\n      left: auto;\n    }\n  }\n\n  //  $$  INDETERMINATE TOGGLE\n  //  ----------------------------------------------------------------------------\n  &--indeterminate {\n    &__inner {\n      display: none;\n    }\n\n    &::after {\n      left: 50%;\n      transform: translate(-50%, 0);\n    }\n  }\n\n  //  $$  FOCUSED TOGGLE\n  //  ----------------------------------------------------------------------------\n  &:focus-visible {\n    outline: none;\n    box-shadow: var(--bs-focus-ring);\n  }\n\n  //  $$  DISABLED TOGGLE\n  //  ----------------------------------------------------------------------------\n  &--disabled,\n  &[disabled] {\n    cursor: not-allowed;\n    opacity: 0.5;\n\n    &::after {\n      cursor: not-allowed;\n      transition-property: none;\n    }\n  }\n}\n\n.d-toggle-group {\n  gap: var(--su8);\n}\n","//@import (reference) '../variables/icons.less';\n\n//\n//  DIALTONE\n//  COMPONENTS: PRESENCE\n//\n//  These are presence classes for Dialpad's design system Dialtone.\n//  For further documentation of these and other classes,\n//  visit https://dialpad.design/components/presence\n\n.d-presence {\n  --color-border-base: var(--black-100);\n  --color-border-offline: var(--black-400);\n  --color-background-base: var(--white);\n  --color-background-active: var(--green-400);\n  --color-background-busy: var(--red-300);\n  --color-background-away: var(--gold-200);\n  --color-background-offline: var(--white);\n\n  display: inline-block;\n  box-sizing: border-box;\n  border-color: var(--color-border-base);\n  border-style: solid;\n  border-width: var(--su2);\n  border-radius: 50%;\n\n  &__inner {\n    box-sizing: border-box;\n    width: var(--su8);\n    height: var(--su8);\n    background-color: var(--color-background-base);\n    border: none;\n    border-radius: 50%;\n  }\n\n  &__inner--active {\n    background-color: var(--color-background-active);\n  }\n\n  &__inner--busy {\n    background-color: var(--color-background-busy);\n  }\n\n  &__inner--away {\n    background-color: var(--color-background-away);\n  }\n\n  &__inner--offline {\n    background-color: var(--color-background-offline);\n    border-color: var(--color-border-offline);\n    border-style: solid;\n    border-width: var(--su2);\n  }\n}\n","//\n//  DIALTONE\n//  UTILITIES: ICONS\n//\n//  These are icon component classes for Dialpad's design system Dialtone.\n//  For further documentation of these and other classes,\n//  visit https://dialpad.design/components/icon.html\n//\n//  TABLE OF CONTENTS\n//  • SIZE\n//\n//  ============================================================================\n//  $   SIZE\n//  ----------------------------------------------------------------------------\n.d-icon {\n  --icon-base-scale: 0.8rem; // replace with global variable eventually\n  --icon-size-100: calc(var(--icon-base-scale) * 1.5);\n  --icon-size-200: calc(var(--icon-base-scale) * 1.75);\n  --icon-size-300: calc(var(--icon-base-scale) * 2.25);\n  --icon-size-400: calc(var(--icon-base-scale) * 2.5);\n  --icon-size-500: calc(var(--icon-base-scale) * 3);\n  --icon-size-600: calc(var(--icon-base-scale) * 4);\n  --icon-size-700: calc(var(--icon-base-scale) * 4.75);\n  --icon-size-800: calc(var(--icon-base-scale) * 6);\n  --icon-size: var(--icon-size-500);\n\n  width: var(--icon-size);\n  height: var(--icon-size);\n  fill: currentColor;\n\n  &--size-800 {\n    --icon-size: var(--icon-size-800);\n  }\n\n  &--size-700 {\n    --icon-size: var(--icon-size-700);\n  }\n\n  &--size-600 {\n    --icon-size: var(--icon-size-600);\n  }\n\n  &--size-500 {\n    --icon-size: var(--icon-size-500);\n  }\n\n  &--size-400 {\n    --icon-size: var(--icon-size-400);\n  }\n\n  &--size-300 {\n    --icon-size: var(--icon-size-300);\n  }\n\n  &--size-200 {\n    --icon-size: var(--icon-size-200);\n  }\n\n  &--size-100 {\n    --icon-size: var(--icon-size-100);\n  }\n}\n","//\n//  DIALTONE\n//  TOKENS: ICONS\n//\n//  These are icon variables for Dialpad's design system Dialtone.\n//  For further documentation of these and other classes,\n//  visit https://dialpad.design/\n//\n//  ============================================================================\n//  $   ICON SIZES\n//  ----------------------------------------------------------------------------\n//  OLD VARIABLES\n@icon-size14:     1.4rem;\n@icon-size16:     1.6rem;\n@icon-size18:     1.8rem;\n@icon-size20:     2rem;\n@icon-size24:     2.4rem;\n@icon-size32:     3.2rem;\n@icon-size48:     4.8rem;\n@icon-size64:     6.4rem;\n\n//  NEW VARIABLES\n@icon12:                        1.2rem;\n@icon14:                        1.4rem;\n@icon16:                        1.6rem;\n@icon18:                        1.8rem;\n@icon20:                        2rem;\n@icon24:                        2.4rem;\n@icon32:                        3.2rem;\n@icon48:                        4.8rem;\n@icon64:                        6.4rem;\n\n@icon-vars: {\n  icon12:                       @icon12;\n  icon14:                       @icon14;\n  icon16:                       @icon16;\n  icon18:                       @icon18;\n  icon20:                       @icon20;\n  icon24:                       @icon24;\n  icon32:                       @icon32;\n  icon48:                       @icon48;\n  icon64:                       @icon64;\n}\n","//@import (reference) '../variables/colors.less';\n//@import (reference) '../utilities/internals.less';\n\n//\n//  DIALTONE\n//  UTILITIES: BACKGROUNDS\n//\n//  These are border utility classes for Dialpad's design system Dialtone.\n//  For further documentation of these and other classes,\n//  visit https://dialpad.design/utilities/borders\n//\n//  TABLE OF CONTENTS\n//  • BACKGROUND ATTACHMENT\n//  • BACKGROUND CLIP\n//  • BACKGROUND POSITION\n//  • BACKGROUND REPEAT\n//  • BACKGROUND SIZE\n//  • GRADIENTS\n//  • PATTERNS\n//\n//  ----------------------------------------------------------------------------\n//  --  Create background stops for most colors\n#d-internals #gradient-stops(black);\n#d-internals #gradient-stops(orange);\n#d-internals #gradient-stops(purple);\n#d-internals #gradient-stops(magenta);\n#d-internals #gradient-stops(gold);\n#d-internals #gradient-stops(red);\n#d-internals #gradient-stops(green);\n\n//  --  A few additional colors that the gradient-stops mixin misses\n#d-internals #gradient-classes(white);\n#d-internals #gradient-classes(black-900);\n#d-internals #gradient-classes(black-800);\n#d-internals #gradient-classes(black-700);\n\n//  ============================================================================\n//  $  BACKGROUND ATTACHMENT\n//  ----------------------------------------------------------------------------\n.d-bga-scroll { background-attachment: scroll !important; }\n.d-bga-fixed { background-attachment: fixed !important; }\n.d-bga-local { background-attachment: local !important; }\n.d-bga-unset { background-attachment: unset !important; }\n\n//  ============================================================================\n//  $  BACKGROUND CLIP\n//  ----------------------------------------------------------------------------\n.d-bgc-border-box { background-clip: border-box !important; }\n.d-bgc-padding-box { background-clip: padding-box !important; }\n.d-bgc-content-box { background-clip: content-box !important; }\n\n.d-bgc-text {\n  color: transparent !important;\n  -webkit-background-clip: text !important;\n  background-clip: text !important;\n}\n.d-bgclip-unset { background-clip: unset !important; }\n\n\n//  ============================================================================\n//  $  BACKGROUND POSITION\n//  ----------------------------------------------------------------------------\n.d-bgp-t { background-position: top !important; }\n.d-bgp-tr { background-position: top right !important; }\n.d-bgp-r { background-position: right !important; }\n.d-bgp-br { background-position: bottom right !important; }\n.d-bgp-b { background-position: bottom !important; }\n.d-bgp-bl { background-position: bottom left !important; }\n.d-bgp-l { background-position: left !important; }\n.d-bgp-tl { background-position: top left !important; }\n.d-bgp-center { background-position: center !important; }\n.d-bgp-unset { background-position: unset !important; }\n\n\n//  ============================================================================\n//  $  BACKGROUND REPEAT\n//  ----------------------------------------------------------------------------\n.d-bgr-repeat-x { background-repeat: repeat-x !important; }\n.d-bgr-repeat-y { background-repeat: repeat-y !important; }\n.d-bgr-repeat { background-repeat: repeat !important; }\n.d-bgr-space { background-repeat: space !important; }\n.d-bgr-none { background-repeat: no-repeat !important; }\n.d-bgr-unset { background-repeat: unset !important; }\n\n\n//  ============================================================================\n//  $  BACKGROUND SIZE\n//  ----------------------------------------------------------------------------\n.d-bgs-contain { background-size: contain !important; }\n.d-bgs-cover { background-size: cover !important; }\n.d-bgs-var { background-size: var(--bgg-size, 100% 100%) !important; }\n.d-bgs-auto { background-size: auto !important; }\n.d-bgs-unset { background-size: unset !important; }\n\n\n//  ============================================================================\n//  $   GRADIENTS\n//  ----------------------------------------------------------------------------\n.d-bgg-to-t { background-image: linear-gradient(to top, var(--bgg-from) 0%, var(--bgg-to) 100%) !important; }\n.d-bgg-to-tr { background-image: linear-gradient(to top right, var(--bgg-from) 0%, var(--bgg-to) 100%) !important; }\n.d-bgg-to-r { background-image: linear-gradient(to right, var(--bgg-from) 0%, var(--bgg-to) 100%) !important; }\n.d-bgg-to-br { background-image: linear-gradient(to bottom right, var(--bgg-from) 0%, var(--bgg-to) 100%) !important; }\n.d-bgg-to-b { background-image: linear-gradient(to bottom, var(--bgg-from) 0%, var(--bgg-to) 100%) !important; }\n.d-bgg-to-bl { background-image: linear-gradient(to bottom left, var(--bgg-from) 0%, var(--bgg-to) 100%) !important; }\n.d-bgg-to-l { background-image: linear-gradient(to left, var(--bgg-from) 0%, var(--bgg-to) 100%) !important; }\n.d-bgg-to-tl { background-image: linear-gradient(to top left, var(--bgg-from) 0%, var(--bgg-to) 100%) !important; }\n.d-bgg-radial { background-image: radial-gradient(var(--bgg-from) 0%, var(--bgg-to) 100%) !important; }\n.d-bgg-conic { background-image: conic-gradient(var(--bgg-from) 0%, var(--bgg-to) 100%) !important; }\n.d-bgg-none { background-image: none !important; }\n#d-internals #generate-hover-focus(d-bgg-none, {.d-bgg-none();});\n.d-bgg-unset { background-image: unset !important; }\n#d-internals #generate-hover-focus(d-bgg-unset, {.d-bgg-unset();});\n\n\n//  ============================================================================\n//  $   PATTERNS\n//  ----------------------------------------------------------------------------\n//  --  BASE CLASS\n.d-bgg-pattern {\n    position: relative;\n    padding-left: var(--su24) !important;\n\n    &::after {\n        position: absolute;\n        top: var(--su1);\n        bottom: var(--su1);\n        left: var(--su1);\n        min-width: 2rem;\n        min-height: 2rem;\n        background-image: var(--bgg-pattern);\n        background-repeat: repeat-y;\n        background-position: top left;\n        background-clip: content-box;\n        border-radius: 0.3rem;\n        content: '';\n    }\n}\n\n//  --  EACH PATTERN\n.d-bgg-pattern-blob-dark { --bgg-pattern: var(--bgg-pattern-blob-dark) !important; }\n.d-bgg-pattern-chevrons-dark { --bgg-pattern: var(--bgg-pattern-chevrons-dark) !important; }\n.d-bgg-pattern-crosses-dark { --bgg-pattern: var(--bgg-pattern-crosses-dark) !important; }\n.d-bgg-pattern-crosshatch-dark { --bgg-pattern: var(--bgg-pattern-crosshatch-dark) !important; }\n.d-bgg-pattern-dot-dash-dark { --bgg-pattern: var(--bgg-pattern-dot-dash-dark) !important; }\n.d-bgg-pattern-dots-circles-dark { --bgg-pattern: var(--bgg-pattern-dots-circles-dark) !important; }\n.d-bgg-pattern-horz-stripes-dark { --bgg-pattern: var(--bgg-pattern-horz-stripes-dark) !important; }\n.d-bgg-pattern-slanted-stripes-dark { --bgg-pattern: var(--bgg-pattern-slanted-stripes-dark) !important; }\n.d-bgg-pattern-steps-dark { --bgg-pattern: var(--bgg-pattern-steps-dark) !important; }\n.d-bgg-pattern-stripe-dark { --bgg-pattern: var(--bgg-pattern-stripe-dark) !important; }\n\n.d-bgg-pattern-blob-light { --bgg-pattern: var(--bgg-pattern-blob-light) !important; }\n.d-bgg-pattern-chevrons-light { --bgg-pattern: var(--bgg-pattern-chevrons-light) !important; }\n.d-bgg-pattern-crosses-light { --bgg-pattern: var(--bgg-pattern-crosses-light) !important; }\n.d-bgg-pattern-crosshatch-light { --bgg-pattern: var(--bgg-pattern-crosshatch-light) !important; }\n.d-bgg-pattern-dot-dash-light { --bgg-pattern: var(--bgg-pattern-dot-dash-light) !important; }\n.d-bgg-pattern-dots-circles-light { --bgg-pattern: var(--bgg-pattern-dots-circles-light) !important; }\n.d-bgg-pattern-horz-stripes-light { --bgg-pattern: var(--bgg-pattern-horz-stripes-light) !important; }\n.d-bgg-pattern-slanted-stripes-light { --bgg-pattern: var(--bgg-pattern-slanted-stripes-light) !important; }\n.d-bgg-pattern-steps-light { --bgg-pattern: var(--bgg-pattern-steps-light) !important; }\n.d-bgg-pattern-stripe-light { --bgg-pattern: var(--bgg-pattern-stripe-light) !important; }\n","//@import (reference) '../variables/spacing.less';\n//@import (reference) '../variables/borders.less';\n//@import (reference) '../utilities/internals.less';\n//\n//  DIALTONE\n//  UTILITIES: BORDERS\n//\n//  These are border utility classes for Dialpad's design system Dialtone.\n//  For further documentation of these and other classes,\n//  visit https://dialpad.design/utilities/borders\n//\n//  TABLE OF CONTENTS\n//  • BORDERS\n//    - COLOR\n//    - DIRECTION\n//    - RADIUS\n//    - STYLE\n//    - WIDTH\n//  • DIVIDERS\n//    - VERTICAL DIVIDERS\n//    - HORIZONTAL DIVIDERS\n//\n//  ============================================================================\n//  $  BORDERS\n//  ============================================================================\n//  $$  COLOR\n//  ----------------------------------------------------------------------------\n.d-bc-transparent { border-color: transparent !important; }\n.d-bc-current { border-color: currentColor !important; }\n.d-bc-unset { border-color: unset !important; }\n\n\n//  $$ DIRECTION\n//  ----------------------------------------------------------------------------\n.d-ba { border: var(--su1) solid !important; }\n.d-bt { border-top: var(--su1) solid !important; }\n.d-br { border-right: var(--su1) solid !important; }\n.d-bb { border-bottom: var(--su1) solid !important; }\n.d-bl { border-left: var(--su1) solid !important; }\n.d-bx { .d-br(); .d-bl(); }\n.d-by { .d-bt(); .d-bb(); }\n.d-ba-none { border: none !important; }\n.d-ba-unset { border: unset !important; }\n\n\n//  ============================================================================\n//  $  BORDER RADIUS\n//  ============================================================================\n//  $$ ALL\n//  ----------------------------------------------------------------------------\n#d-internals #auto-generate(\n    '.d-bar',\n    { .template(@value) { border-radius: @value !important; } },\n    @su0 @su2 @su4 @su8 @su12 @su16 @su24 @su32 unset\n);\n.d-bar-circle { border-radius: @br-circle !important; }\n.d-bar-pill { border-radius: @br-pill !important; }\n\n//  $$ TOP\n//  ----------------------------------------------------------------------------\n#d-internals #auto-g